<template>
  <div class="article-content ">
    <div class="anci_header_content">
      <div class="article-title">
        <h2>{{ city }}</h2>
      </div>
    </div>
    <!-- //文化 -->
    <div v-if="culture.length">
      <p><span class="bjh-h3">文化</span></p>
      <div v-for="(item,index) in culture" :key='index'>
        <div class="img-container">
          <img
            class="large"
            data-loadfunc="0"
            :src="item.img"
            data-loaded="0"
          />
          <span class="bjh-image-caption"></span>
        </div>
        <p><span class="bjh-strong">{{ item.title}}</span></p>
        <p>{{ item.content }}</p>
      </div>
    </div>


    <!-- 风俗礼仪 -->
    <div v-if="etiquette.length">
      <p><span class="bjh-h3">风俗礼仪</span></p>
      <div v-for="(item,index) in etiquette" :key='index'>
        <p>{{ item }}</p>
        <div>{{'\n'}}</div>
      </div>
    </div>

    <!-- 节庆日 -->
    <div v-if="festival.length">
      <p><span class="bjh-h3">节庆日</span></p>
      <div v-for="(item,index) in festival" :key='index'>
        <p>{{ item }}</p>
        <div>{{'\n'}}</div>
      </div>
    </div>



    <!-- 当地体验 -->
    <div v-if="experience.length">
      <p><span class="bjh-h3">当地体验</span></p>
      <div v-for="(item,index) in experience" :key='index'>
        <p><span class="bjh-strong">{{ item.title }}</span></p>

        <p>{{ item.content }}</p>
        <div>{{'\n'}}</div>
        <div class="img-container">
          <img
            class="large"
            data-loadfunc="0"
            :src="item.img"
            data-loaded="0"
          />
          <span class="bjh-image-caption"></span>
        </div>
      </div>
    </div>

    <!-- 实用信息 -->
    <div v-if="information.length">
      <p><span class="bjh-h3">实用信息</span></p>
      <div v-for="(item,index) in information" :key='index'>
        <p><span class="bjh-strong">{{ item.title }}</span></p>

        <p>{{ item.content }}</p>
        <div>{{'\n'}}</div>
        
      </div>
    </div>

  </div>



  </div>
</template>

<script>
import newsJson from '../../../static/news';
export default {
  name: '',
  data() {
    return {
      gonglue: {},
      culture: [], //文化
      etiquette: [], //风俗礼仪
      experience:[],//当地体验
      festival:[],//节庆
      information:[],//实用信息
      city: ''
    };
  },
  components: {},
  props: [],
  created() {
    // console.log("创建后")
    // this.activeCity=this.$route.query.city_name
    // this.disCityData()
    //初始化数据源 文化 culture
    this.gonglue = newsJson.data.gonglue;
    this.city = this.gonglue.city;
    var imgs = this.gonglue.cultureImage.split('##');
    var titles = this.gonglue.cultureTitle.split('##');
    var contents = this.gonglue.culture.split('##');
    this.culture = this.initDataList(imgs,titles,contents);
    console.log(this.culture);

    //风俗礼仪
    this.etiquette = this.gonglue.etiquette.split('##');
    console.log(this.etiquette);
    //当地体验
    imgs = this.gonglue.experienceImage.split('##');
    titles = this.gonglue.experienceTitle.split('##');
    contents = this.gonglue.experience.split('##');
    this.experience = this.initDataList(imgs,titles,contents);

    //节庆
    this.festival = this.gonglue.festival.split('##');
    //实用信息 information
    titles = this.gonglue.informationTitle.split('##');
    contents = this.gonglue.information.split('##');
    this.information = this.initDataList([],titles,contents);
  },
  mounted() {
    // console.log("模板编译组件挂载后")
    // console.log(cityJson)
  },
  watch: {},
  beforeUpdate() {
    // console.log("组件更新前")
  },
  updated() {
    // console.log("组件更新后")
  },
  beforeDestory() {
    // console.log("组件销毁前")
  },
  destoryed() {
    // console.log("组件销毁后")
  },
  methods: {

    initDataList(imgs,titles,contents) {
      var lists = [];
      for (var i = 0; i < contents.length; i++) {
        var item = { img: '', title: '', content: contents[i] };

        if (imgs.length > i) {
          var imgPath = 'http://triptomyhome.com/'+imgs[i];
          item.img = imgPath;
        }
        if (titles.length > i) {
          item.title = titles[i];
          console.log(item.title);
        }
        lists.push(item);
      }
      return lists;
    }
  }
};
</script>

<style>
.anci_header_content {
  width: 900px;
  margin: 0 auto;
  padding-bottom: 50px;
}
.article-content p {
  font-size: 16px;
  line-height: 24px;
  color: #333;
  text-align: justify;
  _margin-top: 22px;
  margin: 10px;
}

.article-title h2 {
  font-size: 40px;
  line-height: 58px;
  padding-top: 41px;
  padding-bottom: 21px;
  text-align: justify;
  font-weight: 700;
  font-family: 'Microsoft Yahei', '微软雅黑', '宋体';
}
.article-content div.img-container {
  margin-top: 30px;
}
.bjh-image-caption {
  font-size: 13px;
  color: #999;
  display: block;
  margin-top: 11px;
  text-align: center;
}
.bjh-h3 {
  font-size: 18px;
  font-weight: 700;
  color: #000;
  position: relative;
  padding-left: 9px;
}
.bjh-h3:before {
  content: '';
  display: inline-block;
  height: 18px;
  width: 5px;
  background-color: #3c76ff;
  position: absolute;
  top: 0;
  left: 0;
}
.bjh-strong {
    font-size: 18px;
    color: #333;
    font-weight: 700;
    margin-top: 30px;
}
.div p {
  margin: 1.25rem;
}

</style>



